<template>
  <el-select
    v-model="query"
    filterable
    remote
    reserve-keyword
    placeholder="Please enter a keyword"
    :remote-method="handleSearch"
    :loading="loading"
    @change="handleChange"
  >
    <el-option
      v-for="({ value, label }) in list"
      :key="value"
      :label="label"
      :value="value"
    />
  </el-select>
</template>

<script>
export default {
  props: {
    options: {
      default: () => [],
      type: Array
    },
    type: String,
    name: String,
    labelType: [String, Number],
    initialValue: [String, Number]
  },
  data: () => ({
    query: [],
    list: [],
    loading: false
  }),
  mounted() {
    if (this.initialValue) {
      this.query = this.initialValue
    }
  },
  watch: {
    options(items) {
      this.list = items.map(item => ({
        value: this.name === 'userId' ? item.id : item[`${this.type}_id`],
        label: item[this.labelType]
      }))
    }
  },
  methods: {
    handleChange(value) {
      this.$emit('handleChange', value, this.name)
    },
    handleSearch(query) {
      this.$emit('handleSearch', query)
    }
  }
}
</script>



